import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { api } from "../api/api";

const baseDevUrl = "http://localhost:3001/api"
const baseUrl = "https://cookie-test-server-1903.herokuapp.com/api"

export const getPosts = createAsyncThunk(
  'PostsSlice/getPosts',
  async function(_, {rejectWithValue}){
    try {
      //const ProductionUrl = 'https://cookie-test-server-1903.herokuapp.com/'
      const response = await api.get(`${baseUrl}/getPosts`)
      if (response.statusText != "OK") {
        throw new Error();
      }
      return response.data
    } catch (error) {
      return rejectWithValue(error.message)
    }
  }
)
export const createPost = createAsyncThunk(
  'PostsSlice/createPost',
  async function(postData, {rejectWithValue}){
    try {
      //const ProductionUrl = 'https://cookie-test-server-1903.herokuapp.com/'
      const response = await api.post(`${baseUrl}/createPost`, postData);
      if (response.statusText != "OK") {
        throw new Error();
      }
      console.log(response)
      return response.data
    } catch (error) {
      return rejectWithValue(error.message)
    }
  }
)
export const likePost = createAsyncThunk(
  'PostsSlice/likePost',
  async function(postData, {rejectWithValue}){
    try {
      //const ProductionUrl = 'https://cookie-test-server-1903.herokuapp.com/'
      const response = await api.post(`${baseUrl}/likePost`,postData);
      if (response.statusText != "OK") {
        throw new Error();
      }
      return response.data
    } catch (error) {
      return rejectWithValue(error.message)
    }
  }
)

const PostsSlice = createSlice({
  name: 'PostsSlice',
  initialState: {
    posts: [],
    recommendedPosts: [],
    status: null,
    error: null, 
    arr : []
  }, 
  reducers: {
    likePosts(state, action){
      state.posts = handleLike(state.posts, action.payload.likeId, action.payload.username)
    },
    addComment(state, action){
      state.posts = handleAddComment(state.posts, action.payload.id, action.payload.username, action.payload.text)
    }
  },
  extraReducers: {
    //----------------------- Get POSTS -----------------------------//
    [getPosts.pending]: (state) => {
      state.status = 'pending'
      state.error = null
    },
    [ getPosts.fulfilled ]: (state, action) => {
      state.status = 'fulfilled'
      state.error = null
      state.posts = action.payload
    },
    [ getPosts.rejected ]: (state, action) => {
      state.status = 'rejected'
      state.error = action.payload
    },
    //----------------------- Add POSTS -----------------------------//
    [createPost.pending]: (state) => {
      state.status = 'pending'
      state.error = null
    },
    [ createPost.fulfilled ]: (state, action) => {
      state.status = 'fulfilled'
      state.error = null
    },
    [ createPost.rejected ]: (state, action) => {
      state.status = 'rejected'
      state.error = action.payload
    },
    //----------------------- Like POSTS -----------------------------//
    [likePost.pending]: (state) => {
      state.status = 'pending'
      state.error = null
    },
    [ likePost.fulfilled ]: (state, action) => {
      state.status = 'fulfilled'
      state.error = null
    },
    [ likePost.rejected ]: (state, action) => {
      state.status = 'rejected'
      state.error = action.payload
    }
  },
})

function handleLike(arr, id, username){
  console.log(arr)
  const postIndex = arr.findIndex(post => post.id === id)
  const postLikes = arr[postIndex].likedBy
  if(postLikes.includes(username)){
      arr[postIndex].likedBy.splice(postLikes.indexOf(username), 1)
  }else{
      arr[postIndex].likedBy.push(username)
  }
  return arr
}


function handleAddComment(arr, id, username, text){
  const postIndex = arr.findIndex(post => post.id === id)
  const comments = arr[postIndex].comments
  const NewComment = {
      by: username,
      text: text
  }
  comments.push(NewComment)
  arr[postIndex].comments = comments
  return arr
}


export default PostsSlice.reducer


export const { addComment } = PostsSlice.actions