import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { api } from "../api/api"; 

const baseDevUrl = "http://localhost:3001/api"
const baseUrl = "https://cookie-test-server-1903.herokuapp.com/api"

const initialUser = {
  userId: 0,
  authorized: false,
  username: '',
  email: '',
  posts: [],
  friends: [], 
  memberSince: '',
  profileImage: '',
}

const DevUser = {
  userId: 1,
  authorized: true,
  username: 'Anthony',
  email: 'anthony@gmail.com',
  posts: [],
  friends: [], 
  memberSince: new Date().toDateString(),
  profileImage: 'http://tachyons.io/img/avatar_1.jpg'
}

export const getAuth = createAsyncThunk(
  'UserSlice/getAuth',
  async function(_, {rejectWithValue}){
    try {
      //const ProductionUrl = 'https://cookie-test-server-1903.herokuapp.com/'
      const response = await api.get(`${baseUrl}/auth`);
      console.log(response)
      if (response.statusText != "OK") {
        throw new Error();
      }
      return response.data
    } catch (error) {
      return rejectWithValue(error.message)
    }
  }
)

const UserSlice = createSlice({
  name: 'UserSlice',
  initialState: initialUser, 
  reducers: {
    signOut(state){
      localStorage.removeItem('accessToken')
      return initialUser
    },
    loadUser(state, action){
      const {username, email, id, profileImage} = action.payload.user
      console.log(action.payload.user)
      const { accessToken } = action.payload
      localStorage.setItem('accessToken',accessToken)
      state.username = username
      state.email = email
      state.userId = id
      state.profileImage = profileImage
      state.authorized = true
    },
    authStatus(state, action){
      state.authorized = action.payload
    },
    changeUsername(state, action){
      state.username = action.payload
    },
    changeImage(state, action){
      state.profileImage = action.payload
    }
  },
  extraReducers: {
    //----------------------- Get POSTS -----------------------------//
    [getAuth.pending]: (state) => {
      state.status = 'pending'
      state.error = null
    },
    [ getAuth.fulfilled ]: (state, action) => {
      const { username, email, id, isActivated, profileImage } = action.payload
      console.log(action.payload)
      state.status = 'fulfilled'
      state.error = null
      state.username = username
      state.email = email
      state.userId = id
      state.authorized = true
      state.profileImage = profileImage
    },
    [ getAuth.rejected ]: (state, action) => {
      state.status = 'rejected'
      state.error = action.payload
    }
  }
})

export default UserSlice.reducer


export const { authStatus, signOut, changeUsername, changeImage, loadUser } = UserSlice.actions